import React from 'react';
import {Button, FormLayout, Panel, PanelHeader, Textarea} from '@vkontakte/vkui';

interface SendProps {
    id: string;
}

interface SendState {

}

class SendComponent extends React.Component<SendProps, SendState> {

    render() {
        return (
            <Panel
                id="send"
                className="header"
            >
                <PanelHeader></PanelHeader>
                <img
                    className="Persik"
                    src={'./img/9.jpg'}
                />
                <FormLayout>
                    <Textarea
                        top="  , ,
                           ,      ."
                        placeholder=",  ....."
                    />
                    <Button size="xl" level="secondary"></Button>
                </FormLayout>
            </Panel>

        );

    }
}

export const Send = (SendComponent);



